#lobby {
    text-align: center;


    display: grid;
    grid-template-rows: minmax(0, 1fr);
  
    color: #fefefe;
  
    h1 {
      margin: 1vh;
      font-size: 1.6vmax;
      font-weight: 300;
      letter-spacing: 0.1vw;
      padding: 0vw 2vw 1.2vw 2vw;
      border-style: solid;
      border-color: #fafafa;
      border-width: 0 0 0.1vw 0;
    }

    a {
      margin: 1vh;
      font-size: 1.3vmax;
      font-weight: 200;
      padding: 0vw 2vw 1.2vw 2vw;
      border-style: solid;
      border-color: #fafafa;
      border-width: 0 0 0.1vw 0;
    }
  }