@mixin scrollDefault() {
  &:hover::-webkit-scrollbar-thumb {
    background-color: rgba(255, 255, 255, 0.5);
  }

  &::-webkit-scrollbar {
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 50px;
    width: 0.36vw;
    height: 0.4vh;
    transition: width 1s ease-in-out;
  }

  &::-webkit-scrollbar-thumb {
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 20vw;
  }
}

#sort_page {
  text-align: center;

  color: #fefefe;

  h1 {
    font-size: 1.6vmax;
    font-weight: 300;
    padding: 0vw 2vw 1.2vw 2vw;
    border-style: solid;
    border-color: #fafafa;
    border-width: 0 0 0.1vw 0;
  }

  #sort_option {
    display: grid;
    grid-template-columns: auto auto;
    padding: 0 10%;

    margin: 0;

    .visual_type,
    .sort_type {
      margin: 0 5%;
    }
  }

  input[type="number"] {
    height: inherit;
    border: solid #fafafac1 1px;
    background-color: #ebdbdb07;
    border-radius: 0.2vw;

    max-height: 90%;

    padding: 0 5% 0 5%;
    font-weight: 200;
    font-size: 0.6vw;
    color: inherit;
    font-family: inherit;

    display: flex;
    justify-content: center;
    align-items: center;

    transition: background-color 0.2s cubic-bezier(0.79, 1.38, 1, 1.5);

    &:focus {
      outline: none;
      background-color: #ebdbdb2c;
    }

    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  }

  .tooltip {
    user-select: no;
    position: absolute !important;
    visibility: hidden;

    width: 160%;
    bottom: 100%;
    left: 50% !important;
    margin-left: -80%;

    font-size: 0.7vw !important;
    font-weight: 300 !important;

    background-color: rgba(44, 43, 43, 0.95) !important;
    color: #fff;

    text-align: center !important;
    padding: 0.1vw 0 !important;
    border-radius: 0.2vw !important;

    z-index: 1 !important;
  }

  button {
    font: inherit;
    background-color: #101010;
    color: #fafafa;
    border: solid #fafafac1 1px;
    border-radius: 0.2vw;
    display: flex;
    justify-content: center;
    align-items: center;

    max-height: 90%;
    aspect-ratio: 1.5 / 1;
  }

  #sort_screen {
    padding: 1% 1.5%;
    display: grid;
    grid-template-columns: 70% 30%;

    height: 80vh;

    #sort_visual {
      border: solid #fafafa 1px;
      border-radius: 0.2vw;

      margin: 0 1%;

      height: 100%;
    }

    #sort_data {
      border: solid #fafafa 1px;
      border-radius: 0.2vw;

      margin: 0 1%;

      height: 100%;

      display: grid;
      grid-template-rows: 10% 90%;

      #sort_player {
        border-radius: 0.2vw;

        display: grid;
        grid-template-columns: repeat(5, 1fr);
        text-align: center;
        align-items: center;
        padding: 0 1vw 0 1vw;
        user-select: none;
        max-height: 100%;

        button {
          position: relative;
          height: inherit;
          height: 70%;
          width: 70%;

          font-size: 1vw;

          transition: background-color 0.1s cubic-bezier(0.79, 1.38, 1, 1.5);
          &:hover .tooltip {
            visibility: visible;
          }
        }

        input[type="number"] {
          height: 80%;
          width: 80%;
          font-size: 1.2vw;
        }

        label {
          font-size: 1vw;
        }

        #sort_delay {
          &::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
          }
          width: 100%;
        }
      }
    }
  }
}
